$dark-grey: rgba(32, 37, 40, .8);
$card-background: rgba(145, 145, 145, 0.8);
$element-background: rgba(51, 51, 51, .5);
$red-font: #963025;

.character__cards {
  background: $card-background;
  border: 2px solid white;
  color: white;
  width: 22%;
  margin: 1% 1%;
  height: 68vh;
  text-align: center;
  position: relative;
}

.character__card--element {
  background: $dark-grey;
  font-size: 1.5em;
  margin-top: 2%;
  margin-bottom: 2%;
}

.character__card--div {
  margin-top: 6%;
}

.character__card--films {
  font-size: 1em;
  margin-top:2%;
  margin-block-end: 0em;
  margin-block-start: 1%;
  background-color: $element-background;
  color:white;
}

.character__card--info {
  color: $red-font;
  font-size: 1.25em;
  text-shadow: 1px 0px $card-background;
}

.article__favorite-button {
  width: 40px;
  -webkit-transition: -webkit-transform .8s ease-in-out;
  transition: transform .2s ease-in-out;
}

.article__favorite-button:hover {
  cursor:pointer;
  -webkit-transform: rotate(70deg);
  transform: rotate(70deg);
}

.favorite-button-container {
  background-color: white;
}
